<template>
  <div>
    <van-tabs v-model="active">
      <div v-for="(tp, index) in typelist" :key="index">
        <van-tab :title="tp.mTypeName" :name="tp.mTypeCode">
          <div class="title">
            {{ tp.subTitle }}
          </div>

          <div style="margin-bottom:100px" v-if="tp.mTypeCode == '100'">
            <div :key="index2" v-for="(item, index2) in marketlist">
              <div style="font-size:20px">{{ item.beginDate }}开始</div>
              <van-card
            
                price="5.00"
                origin-price="10.00"
                desc="原价10元 限时5元"
                title="高级大床房 "
                thumb="./images/dcf.jpg"
              >
                <div slot="tags"></div>
                <div slot="footer">
                  <!-- <van-button size="normal" type="danger" @click="rush(item)">抢购</van-button> -->
                  <van-button
                    size="normal"
                    type="warning"
                    @click="remindMe(item)"
                    >提醒我</van-button
                  >
                </div>
              </van-card>

              <!-- <div>
                <div style="font-size:20px">12:00开始</div>
                <van-card
                  v-for="item in 2"
                  :key="item"
                  price="5.00"
                  origin-price="10.00"
                  desc="原价10元 限时5元"
                  title="精品双床房 "
                  thumb="./images/scf.jpg"
                >
                  <div slot="tags"></div>
                  <div slot="footer">
                    <van-button
                      size="normal"
                      type="warning"
                      @click="remindMe(item)"
                      >提醒我</van-button
                    >
                  </div>
                </van-card>
              </div> -->
            </div>
          </div>
          <div v-if="tp.mTypeCode == '101'">
            <div style="margin-bottom:100px">
              <van-card
                v-for="(item, index) in 5"
                :key="item"
                num="1"
                price="5.00"
                desc="标间满100元可抵20元"
                title="标间20元优惠券 "
                thumb="https://img.yzcdn.cn/vant/ipad.jpeg"
              >
                <div slot="footer">
                  <van-button
                    size="normal"
                    type="danger"
                    @click="groupbuy(item)"
                    >{{ index % 2 == 0 ? "开团" : "拼团" }}</van-button
                  >
                </div>
              </van-card>
            </div>
          </div>

          <div v-if="tp.mTypeCode == '103'">
            <div style="margin-bottom:100px">
              <van-card
                v-for="item in 5"
                :key="item"
                num="1"
                price="5.00"
                desc="预购2月后价格"
                title="商务大床房 "
                thumb="https://img.yzcdn.cn/vant/ipad.jpeg"
              >
                <div slot="footer">
                  <van-button
                    size="normal"
                    type="danger"
                    @click="bookingbuy(item)"
                    >预购</van-button
                  >
                </div>
              </van-card>
            </div>
          </div>

          <div v-if="tp.mTypeCode == '102'">
            <div
              style="display: flex;margin:5px 10px 20px 10px;"
              v-for="(item, index) in marketlist"
              :key="index"
            >
              <div class="radial-gradient">
                <div style="text-align: center;">
                  <div style="display: inline-block;">
                    <div class="price">￥{{ item.oriPrice }}</div>
                    <div class="des">
                      {{ item.infoName }}
                    </div>
                  </div>
                </div>
              </div>
              <div class="right">
                <div class="top">
                  <!-- <span class="limit">限品类券</span> -->
                  <span>{{ item.remark }}</span>
                </div>
                <div class="center">
                  <span class="effectTime"
                    ><span style="color:red;font-size:20px"
                      >￥{{ item.price }}</span
                    >
                    <del
                      v-if="item.oriPrice != null && item.oriPrice > item.price"
                      style="color:rgb(171,171,171);margin-left:10px"
                      >{{ item.oriPrice }}</del
                    >
                  </span>
                  <span class="btnbuy" @click="couponbuy(item)">立即抢购</span>

                  <!-- <span class="effectTime">2020.11.27-2020.11.28</span> -->
                </div>
                <div class="dotted"></div>
                <div class="bottom">
                  <span>剩余数量：{{ item.lastQty }}</span>
                </div>
              </div>
            </div>
          </div>
        </van-tab>
      </div>
    </van-tabs>
  </div>
</template>
<script>
import axiosurlService from "../../../resource/axiosurlService";
export default {
  data() {
    return {
      active: 0,
      marketlist: [],
      typelist: []
    };
  },
  watch: {
    active(newVal, oldVal) {
      this.loaddata(newVal);
    }
  },
  created() {
    axiosurlService.getMarketType().then(rs => {
      console.log(rs);
      if (rs.data.success) {
        this.typelist = rs.data.data;

        // alert(this.typelist.indexOf('100',0));
      }
    });
  },
  methods: {
    loaddata() {
      console.log(this.active);
      this.marketlist = [];
      axiosurlService.getMarketByType({ typeCode: this.active }).then(rs => {
        console.log(rs);
        if (rs.data.success) {
          rs.data.data.forEach(item => {
            var mom = this.$moment(item.beginDate);
            var time = mom.format("MM-DD HH:mm");
            // time = mom.format("DD号HH点");
            // time=  mom.day()+"";
            // var arr = time.split(":");
            // if (arr[arr.length - 1] == "00") {
            //   time = mom.format("HH");
            //   time += "点 ";
            // }

            item.beginDate = time;

            //
          });
          this.marketlist = rs.data.data;
        }
      });
    },
    goCalender(query) {
      this.$router.push({
        path: "/calender/calender",
        query: query
      });
    },

    bookingbuy() {
      //预购
      var query = {
        from: "marketing",
        marketingId: "86bfb79f-aca3-418a-a69f-c48d45ee1695"
      };
      this.goCalender(query);
    },
    remindMe(item) {
      //提醒我
      console.log(item);
  
      var paras = {
        marketId: item.id,
        isCancel: false
      };
      axiosurlService.marketNotice(paras).then(rs => {
        console.log(rs);
     
          this.$toast(rs.data.message);
        
      });
    },
    rush(item) {
      var now = new Date();
      var date2 = new Date(item.beginDate);
      console.log(now);
      console.log(date2);
      console.log(item.beginDate);
      console.log(now >= date2);
      if (item.beginDate <= now) {
        this.$toast("活动未开始");
        return;
      } else {
        // this.$toast("活动未开始");
      }
      // return;
      //秒杀抢购
      console.log(item);

      var query = {
        from: "marketing",
        marketingId: "86bfb79f-aca3-418a-a69f-c48d45ee1695"
      };
      this.goCalender(query);

      // var paras = {
      //   marketTypeCode: 1,
      //   marketId: "86bfb79f-aca3-418a-a69f-c48d45ee1695"
      // };
      // var query = {};
      // axiosurlService.queryMarket(paras).then(rs => {
      //   console.log(rs);
      //   if (rs.data.success) {
      //     this.$router.push({
      //       path: "/ordering/ordering",
      //       query: query
      //     });
      //   }
      // });
    },
    groupbuy(item) {
      //团购
      console.log(item);

      var query = {
        from: "marketing",
        marketingId: "86bfb79f-aca3-418a-a69f-c48d45ee1695"
      };
      this.goCalender(query);

      // var paras = {
      //   marketTypeCode: 1,
      //   marketId: "86bfb79f-aca3-418a-a69f-c48d45ee1695"
      // };
      // var query = {};
      // axiosurlService.queryMarket(paras).then(rs => {
      //   console.log(rs);
      //   if (rs.data.success) {
      //     this.$router.push({
      //       path: "/ordering/ordering",
      //       query: query
      //     });
      //   }
      // });
    },
    couponbuy(item) {
      //优惠券
      console.log(item);
      var paras = {
        marketTypeCode: "102",
        marketId: item.id
      };

      var query = paras;
      axiosurlService.queryMarket(paras).then(rs => {
        console.log(rs);
        if (rs.data.success) {
          this.$router.push({
            path: "./couponPay",
            query: query
          });
        }
      });
    }
  }
};
</script>
<style>
.title {
  text-align: center;
}
.van-card__title {
  margin-top: 0px;
  margin-bottom: 10px;
}
.van-card__footer {
  width: 80px;
  /* float: right; */
  position: absolute;
  right: 10px;
  bottom: 10px;
}
</style>

<style >
@import "./css/coupon.css";
</style>


