<template>
    <div class="tansincontent">
        <!-- <v-header titles='会员中心' isshow='yes'></v-header> -->
        <div class="myCenter" v-if='userInfo'>
            <div class="topBox">
                <div class="detailBox">
                    <div class="head">
                        <img src="./images/user.png" />
                    </div>
                    <span>尊敬的用户：【{{userInfo.userName}}】</span>
                    <div class="edit" @click="changeName()">
                        <img src="./images/edit.png" />
                    </div>
                </div>
                <div class="cardBox" >
                    <span class="type" >{{userInfo.isVip?userInfo.vipName:'非会员'}}</span>
                    <p class="wordTip" v-if='userInfo.isVip'>卡号：{{userInfo.cardNo}}</p>
                    <div v-if='!userInfo.isVip' class='register' @click="register"> 注册会员</div>
                    <!-- <p class="wordTip">再住<span class="day">5</span>夜间可升级为金会员</p> -->
                </div>
                <div v-if="userInfo.isVip" class="equityBox">
                    <h2>您的权益</h2>
                    <ul v-for="(item,index) in userInfo.rights" :key='index'>
                        <li>{{item}}</li>
                    </ul>
                </div>
            </div>
            <div class="iconAll">
                <div @click="goOrder(0)">
                    <div>
                        <img src="./images/icon-1.png" />
                    </div>
                    <div >全部订单</div>
                </div>
                 <div>
                    <div @click="goOrder(102)">
                        <img src="./images/icon-2.png" />
                    </div>
                    <div>待评论</div>
                </div>
                <div>
                    <div @click="goOrder(11)">
                        <img src="./images/icon-3.png" />
                    </div>
                    <div>待入住</div>
                </div>
            </div>
            <div class="line"></div>
            <div class="wallet">
                <h2 class="title"><span>我的钱包</span></h2>
                <div class="iconAll"  v-show="isMember">
                    <div>
                        <div @click='goRecharge()'>
                            <img src="./images/icon-4.png" />
                        </div>
                        <div>储值</div>
                        <div class="bigSize">￥{{userInfo.balance}}</div>
                    </div>
                    <div>
                        <div>
                            <img src="./images/icon-5.png" />
                        </div>
                        <div>积分</div>
                        <div class="bigSize">{{userInfo.score}}</div>
                    </div>
                    <div @click='goCupon()'>
                        <div>
                            <img src="./images/icon-6.png" />
                        </div>
                        <div>优惠券</div>
                        <div class="bigSize">{{userInfo.couponQty}}张</div>
                    </div>
                </div>
            </div>
            <!-- <div class="line"></div> -->
            <!-- <div class="wallet">
                <h2 class="title"><span>我的券包</span></h2>
                <div class="iconAll">
                    <div>
                        <div>
                            <img src="./images/icon-7.png" />
                        </div>
                        <div>优惠券</div>
                        <div class="bigSize">0￥</div>
                    </div>
                    <div>
                        <div>
                            <img src="./images/icon-8.png" />
                        </div>
                        <div>早餐券</div>
                        <div class="bigSize">0￥</div>
                    </div>
                    <div>
                        <div>
                            <img src="./images/icon-9.png" />
                        </div>
                        <div>促销券</div>
                        <div class="bigSize">0￥</div>
                    </div>
                </div>
            </div> -->
            <div class="line"></div>
            <van-button  v-show="isRecharge"  class="chargeButton" @click='goRecharge()'>充值</van-button>
           
             <div class="line"></div>
            <van-button class="logOUt" @click='loginOut'>退出当前账户</van-button>
        </div>
    </div>
</template>

<script>
import Vue from 'vue'
import { Icon,Button } from 'vant'
import axiosurlService from '../../../resource/axiosurlService'
export default {
  name: 'ordering',
   data () {
    return {
       userInfo:null,
       isMember:false,
       isRecharge:false
    }
  },
  components: {  
    
  },
  watch:{

  },
  methods: {
      goCupon(){
         let that = this;
         that.$router.push({
            path: "/coupon/coupon",
            query:{
                isShowAll:true
            }
        });
      },
      goRecharge(){
         let that = this;
         if(that.isRecharge)
         {
           that.$router.push({
            path: "/pay/memberRecharge",
            query:{
                
            }
        });

         }
       
      },
   
      changeName(){
           let that = this;
            that.$router.push({
                path: "/my/my",
            });
      },
      loginOut(){
            let that = this;
            that.$router.push({
                path: "/login/login",
            });
      },
      register(){
           let that = this;
            that.$router.push({
                path: "/register/register",
            });
      },
      goOrder(state){
           let that = this;
            that.$router.push({
                path: "/orderList/orderList",
                query:{
                    state:state
                }
            });
      },
    userdetail(){
      let that = this;
      let param = {} //请求参数
     	axiosurlService.userdetail(param).then(response => {
            let responseData = response.data;
                if(responseData.success == true) {
                   that.userInfo = responseData.data //个人中心
                   that.isMember= responseData.data.isVip;
                    if(response.statusCode==100)
                    {
                       that.isRecharge=true;

                    }
                   console.log(that.userInfo)
                   console.log(that.isMember)
                } else {
                    this.$toast(responseData.message);
                }
			 })
    },
             forbidback(){
         this.goHotel();
},
   goHotel(){
         let that = this;
         var router="";
                   var isgroup =  window.localStorage.getItem("isgroup");
			  console.log(isgroup);
			  if(isgroup==0){
				  router="/houseReservation/houseReservation"
			  }else{
			  router = '/index'

			  }
     that.$router.replace({
                    path: router, query: {
                    isgroup: isgroup
                  }});
   }
  },
  mounted() {
        history.pushState(null, null, document.URL)
        window.addEventListener('popstate', this.forbidback);
      this.userdetail()
    
  }
}
</script>

<style lang='less' src='./less/myCenter.less'>

</style>




